













































































.ingredient {
  cursor: grab;
  &:active {
    cursor: grabbing;
  }
  .ingredient-img {
    width: 140%;
    height: 200px;
    margin-left: -20%;
    transform-origin: 50% 200%;
    // background-color: pink;
    // border-radius: 200px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    @media screen and (max-width: 530px) {
      height: 50vw;
    }
    svg {
      max-height: 100%;
    }
  }
}

.colored-shadow {
  opacity: 0;
  transition: opacity 1s;
  transition-delay: 0.2s;
  .selected & {
    opacity: 1;
  }
}

.ingredient-name {
  font-weight: 500;
  font-size: 1.2rem;
}

.top-color {
  stop-color: var(--top-color);
}
.bottom-color {
  stop-color: var(--bottom-color);
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
