




















































.cart-wrap {
  box-shadow: 0 10px 40px rgba(10, 25, 105, 0.2),
    0 5px 20px rgba(10, 25, 105, 0.1);
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  margin: 40px auto 40px;
  max-width: 400px;
  width: 100%;
  ul {
    padding: 0;
    text-align: left;
    margin: 20px auto;
    li {
      list-style: none;
      margin: 20px 0;
      padding-bottom: 10px;
      border-bottom: 1px solid rgb(204, 204, 204);
    }
  }
}
.price {
  font-weight: 600;
  font-size: 20px;
  float: right;
  color: rgb(87, 87, 87);
}
