.footer {
  background-color: $color_brand_purple;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 1rem;
}

.cart-icon {
  position: relative;
  svg {
    height: 40px;
  }
}
.cart-counter {
  position: absolute;
  top: -10px;
  right: 15px;
  background-color: $color_brand_turquoise;
  color: white;
  font-weight: 800;
  padding-top: 6px;
  height: 30px;
  width: 30px;
  border-radius: 100px;
  animation: scaleIn 0.2s ease-out;
}

@keyframes scaleIn {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  90% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
