  cursor: grab;
  &:active {
    cursor: grabbing;
  }
  svg {
    width: 140%;
    margin-left: -20%;
    transform-origin: 50% 200%;
    @media screen and (max-width: 530px) {
      height: 50vw;
    }
  }
}

.colored-shadow {
  opacity: 0;
  transition: opacity 1s;
  transition-delay: 0.2s;
  .selected & {
    opacity: 1;
  }
}

.top-color {
  stop-color: var(--top-color);
}
.bottom-color {
  stop-color: var(--bottom-color);
}
