.btn {
  padding: 1rem 2rem;
  min-width: 140px;
  border: 0;
  border-radius: 100px;
  color: white;
  font-weight: 700;
  font-size: 1.2rem;
  background-color: #50DBBB;
  cursor: pointer;
  transition: background-color 0.2s; }
  .btn:hover {
    background-color: #26b997; }

body {
  background: linear-gradient(to bottom, white, #c6fff2);
  min-height: 100vh;
  margin: 0; }

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50; }

#nav {
  padding: 10px;
  display: flex;
  justify-content: center; }
  #nav .header-logo {
    height: 70px;
    width: 150px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center; }
    #nav .header-logo.dark {
      background-image: url("./assets/logo.min.svg"); }
  #nav .account-link {
    position: absolute;
    right: 20px;
    top: 20px;
    transition: transform 0.3s ease-out; }
    #nav .account-link:hover, #nav .account-link:active {
      transform: scale(1.2); }
  #nav a {
    font-weight: bold;
    color: #2c3e50; }
    #nav a.router-link-exact-active {
      color: #42b983; }
